'use strict';

var base = require('base-extend-backbone');
var BaseView = base.View;
var VideoView = require('./video.view.js');
var CalendarView = require('./calendar.view');
var ChannelDetailView = require('./banner.view');
var user = require('../../module/util/user.js');
var topbar = require('../../module/topbar/topbar.js');
var loginBox = require('loginBox');
var View = BaseView.extend({
    el: '.container',
    events: {
        'click .nav_v_c li': 'tab',
    },
    rawLoader: function() {
        return '';
    },
    context: function(args) {
        console.log(args);
    },
    beforeMount: function() {
        //  初始化一些自定义属性
        this.scollStatus = 0;
    },
    afterMount: function() {
        //  获取findDOMNode DOM Node
    },
    ready: function() {
        //  初始化
        this._videoBox = this.$el.find('.videoBox');
        this._calenderBox = this.$el.find('.calenderBox');
        this.topbar = new topbar();
        this.videoView = new VideoView();
        this.calendarView = new CalendarView();
        this.channelDetailView = new ChannelDetailView();

        this.onEvents();
    },
    beforeDestroy: function() {
        //  进入销毁之前,将引用关系设置为null
    },
    destroyed: function() {
        //  销毁之后
    },
    tab: function(e) {
        var $this = $(e.currentTarget);
        if ($this.hasClass('on')) return;
        if ($this.hasClass('video')) {
            this._videoBox.show();
            this._calenderBox.hide();
            this.scollStatus = 0;
        } else {
            this._videoBox.hide();
            this._calenderBox.show();
            this.scollStatus = 1;
        }
        $this.addClass('on').siblings().removeClass('on');
    },
    onEvents : function () {
        var self = this;
        function loading() {
            if(self.scollStatus==0){
                if(self.videoView.bStopFetch)return;
            }else{
                if(self.calendarView.bStopFetch)return;
            }

            var $windowScrollTop = $(window).scrollTop();
            var $windowHeight = $(window).height();
            var $documentHeight = $(document).height();
            if (($windowScrollTop + $windowHeight) > $documentHeight - 200) {
                self.scrollIndex++;
                // 调用viewView里面的注册事件
                if(self.scollStatus==0){
                    self.videoView.trigger('events:getVideoList');
                }else{
                    self.calendarView.trigger('events:getCalendarList');
                }

            }
        }
        var load = _.throttle(loading,500);
        $(window).scroll(load);
    },
});

module.exports = View;



